<template>
	<div id="tabbar">
		<Times txt = '首页' wike = 'home' :vctive2 = vctive @chengs = 'velCeng'>
			<img src="../assets/images/ic_tab_home_normal.png" slot = 'moloImg'/>
			<img src="../assets/images/ic_tab_home_active.png" slot = 'activeImg'/>
		</Times>
		<Times txt = '书影音' wike = 'audio' :vctive2 = vctive @chengs = 'velCeng'  >
			<img src="../assets/images/ic_tab_subject_normal.png" slot = 'moloImg'/>
			<img src="../assets/images/ic_tab_subject_active.png" slot = 'activeImg'/>
		</Times>
		<Times txt = '广播' wike = 'broadcast' :vctive2 = vctive @chengs = 'velCeng'  >
			<img src="../assets/images/ic_tab_status_normal.png" slot = 'moloImg'/>
			<img src="../assets/images/ic_tab_status_active.png" slot = 'activeImg'/>
		</Times>
		<Times txt = '小组' wike = 'group' :vctive2 = vctive @chengs = 'velCeng'  >
			<img src="../assets/images/ic_tab_group_normal.png" slot = 'moloImg'/>
			<img src="../assets/images/ic_tab_group_active.png" slot = 'activeImg'/>
		</Times>
		<Times txt = '我的' wike = 'mine' :vctive2 = vctive @chengs = 'velCeng'  >
			<img src="../assets/images/ic_tab_profile_normal.png" slot = 'moloImg'/>
			<img src="../assets/images/ic_tab_profile_active.png" slot = 'activeImg'/>
		</Times>
	</div>
</template>

<script>
	import Times from '../components/times'
	export default{
	  name: 'app',
	  data:function(){
	  	return {
	  		vctive :'home'		
	  	}
	  },
	  methods :{
	  	velCeng :function(val){
	  		this.vctive = val;
	  		
	  	}
	  },
	  components:{
	  	Times,
	  }
	}
</script>

<style>
	#tabbar{width: 100%;height: 0.95rem;border-top: 1px solid #ccc;position: fixed;left: 0;bottom: 0;background: #f7f7f7;z-index: 10;}
	#tabbar img{height: 0.65rem;width: 0.62rem;}
</style>


