<template>
	<div class="broadcastWarg">
		<Topheader></Topheader>
		<Myheader txt = '广播'>
			<img src="../../assets/images/ic_chat_green.png" slot = 'right'/>
			<img src="../../assets/images/ic_status_search_user.png" slot = 'left'/>
		</Myheader>
		<div class="lin_txt">
			<img src="../../assets/images/ic_status_anonymous_header.png" class="linkman" />
			<h2>发现有趣的人</h2>
			<span>关注他们，发现更大的世界</span>
		</div>
		<Attention a_name ='陈苍苍' a_attention = '53288' @chengs = 'receives' v-show = '!shows' >
			<img src="../../assets/images/b_pic_07.jpg" slot = 'attentionImg'/>
			<p slot = 'a_liked'>50</p>
			<p slot = 'a_comment'>29</p>
			<p slot = 'a_text'>我们没有小孩的也很不容易的啊 ！在家被别人的小孩吵，三天两头躲出去不就是为了图个清静吗！出门在外接着被各种熊孩子打扰，坐个飞机小孩喊一路坐个高铁小孩也嚎一路，很不容易！help！哭哭，我化妆我健身我努力维持体面，为什么这么多小孩不肯放过我</p>
		</Attention>
		<Attention a_name ='绿箜' a_attention = '1142' @chengs = 'receives' v-show = '!shows' >
			<img src="../../assets/images/b_pic_10.jpg" slot = 'attentionImg'/>
			<p slot = 'a_liked'>23</p>
			<p slot = 'a_comment'>16</p>
			<p slot = 'a_text'>菜市场的颜色真是漂亮极了。有非常多我喜欢吃的东西,非常多新鲜的蔬菜，水果，海鲜，等等。今天回去要买好多的菜，好好犒劳下自己，最近确实上班辛苦。晚上大餐，红烧肉，清蒸鲈鱼，油焖大虾，是不是隔着手机屏幕都觉得香啊，哈哈，晚上再来放毒.....</p>
		</Attention>
		<Attention a_name ='逆流沙' a_attention = '76689' @chengs = 'receives' v-show = 'shows' >
			<img src="../../assets/images/pic_music_imger_03.jpg" slot = 'attentionImg'/>
			<p slot = 'a_liked'>99</p>
			<p slot = 'a_comment'>58</p>
			<p slot = 'a_text'>我会等你来“大朋友，如果某天我消失了，你会找我吗?”淡淡的话语毫不突兀的出现在手机的屏幕，以以为时间可以给予一个感动的开幕。“小朋友，如果你爱我，你还会让自己消失吗?”这样一个简单而随意的答复，伴随着大朋友敲打手,我们的关系处在怎样的立场?</p>
		</Attention>
		<Attention a_name ='轻描淡写' a_attention = '13752' @chengs = 'receives' v-show = 'shows' >
			<img src="../../assets/images/pic_music_imgyi_07.jpg" slot = 'attentionImg'/>
			<p slot = 'a_liked'>79</p>
			<p slot = 'a_comment'>60</p>
			<p slot = 'a_text'>为什么不说自信呢？不自信的人，普遍内心比较脆弱。一个自信的人，对自己充满信心，做事往往带着积极向上的力量，并时刻充满激情。所有的盲目自信，和空腹自信，都是自以为是。心中要有真才实学，哪怕在不断的试错，但终究能到达攀登高峰的那一天。</p>
		</Attention>
		<Batch @myShows = 'broShow'>
			<div slot = 'advertising' :class="{color:vuls == false ?true:false}">选好了，进入广播</div>
			<span slot = 'b_batch_txt' ><img src="../../assets/images/ic_reshare_large.png" slot = 'b_batchImg' />换一批</span>
		</Batch>
	</div>
</template>

<script>
	import Myheader from '../../components/myheader'
	import Topheader from '../../components/topheader'
	import Attention from './attention'
	import Batch from '../../components/batch'

	export default {
		components :{
			Myheader:Myheader,
			Topheader,
			Attention,
			Batch 
		},
		data : function(){
			return {
				vuls : 'false',
				bloss : 'false',
				shows : ''
			}
		},
		methods : {
			receives:function(vul){
				this.vuls = vul;
			},
			broShow  : function(vuls){
				this.shows = vuls;
			}
		}
	}
</script>


<style>
.broadcastWarg{background: #f7f7f7;min-height: 11.85rem;padding: 0.8rem 0 2.1rem 0;}
.broadcastWarg .lin_txt{padding: 0.4rem;height: 1.22rem;overflow: hidden;}
.broadcastWarg .lin_txt .linkman{height: 1.48rem;width: 1.64rem;margin-left: 0.94rem;float: left;margin-right: 0.3rem;}
.broadcastWarg .lin_txt h2{margin-top: 0.3rem;font-size: 0.26rem;}
.broadcastWarg .lin_txt span{margin-top: 0.15rem;display: block;font-size: 0.24rem;color: #999;}
.broadcastWarg .color{background: #42C055;}
</style>


