<template>
	<div id="times" @click="fn()">
		<span v-show="bol"><slot name = 'moloImg'></slot><br /></span>
		<span v-show="!bol"><slot name = 'activeImg'></slot><br /></span>
		<span :class="{accolor:!bol}">{{ txt }}</span>
	</div>
</template>

<script>
	export default {
	  	props:['txt','wike','vctive2'],
	  	methods:{
	  		fn:function(){
	  			this.$emit('chengs',this.wike)
	  			this.$router.push('/'+this.wike)
	  		}
	  	},
	  	computed:{
	  		bol:function(){
	  			if(this.wike == this.vctive2){
	  				return false;
	  			}
	  			return true;
	  		}
	  	}
	  	
	}
</script>

<style>
	#times{float: left;width: 20%;text-align: center;}
	#times img{width: 0.65rem;}
	#times span{font-size: 0.22rem;;color:#B2B2B2;line-height: 0.22rem;margin-bottom: -0.03rem;display: block;}
	#times .accolor{color: #54C366;font-size: 0.22rem;line-height: 0.22rem;}
</style>


