<template>
	<div class="b_batch">
		<div class="advertising"><slot name = 'advertising'></slot></div>
		<span class="b_batchImg" @click="mybatch()">
			<slot name = 'b_batchImg'></slot>
			<p><slot name = 'b_batch_txt'></slot></p>
		</span>
	</div>
</template>

<script>
	export default {
		data : function(){
			return {
				myShow : 'true'
			}
		},
		methods : {
			mybatch : function(){
				this.$emit('myShows',this.myShow)
				this.myShow = !this.myShow;
				
			}
		}
	}
</script>

<style>
.b_batch{position: fixed;left: 0;bottom: 0.95rem;z-index: 12; height: 0.97rem;width: 100%; background: #fff;box-shadow: 0 -0.05rem 0.1rem 0 #eee;}
.b_batch .b_batchImg{height: 100%;color:#828282;font-size: 0.24rem;}
.b_batch .b_batchImg img{margin-left:0.51rem ;width: 0.42rem;height: 0.42rem;margin-top: 0.25rem;float: left;}
.b_batch .b_batchImg p{float: left;line-height: 0.95rem;}
.b_batch .advertising{float: right;width: 2.68rem;height: 0.54rem;background: #dfdfdf;color: #fefefe;text-align: center;line-height: 0.54rem;border-radius: 0.05rem;margin: 0.21rem 0.3rem 0 0;font-size: 0.24rem;}
</style>


