			<p class="home_jack" slot = "jack">此影评送给那些不离不弃的老粉和船长杰克·斯派罗。</p>
			<img src="../../assets/images/ic_home_pic_03.jpg" slot = 'homenewsImg'/>
			<span slot = "authoron"class="home_zuo">作者：女神的秋裤</span>
		</Homenews>
		<Homenews>
			<p class="home_pirate" slot = "pirate">一周豆瓣热门影视|二十年后，这帮苏格兰老炮儿还是那么嗨</p>
			<p class="home_jack" slot = "jack">三个榜单的冠军与上奏一样，冠军们大概孤独求败....</p>
			<img src="../../assets/images/ic_home_pic_06.jpg" slot = 'homenewsImg'/>
			<span slot = "authoron"class="home_zuo">作者：豆瓣</span>
			<span slot = "authorto"class="home_zuoright">来自栏目：一周热门影视</span>
		</Homenews>
		<Homenews>
			<p class="home_pirate" slot = "pirate">预告片精选|称霸豆瓣电影Top250的男人拍出史上最....</p>
			<p class="home_jack" slot = "jack">诺兰口碑力作《敦刻尔克》内地定档9月1日</p>
			<img src="../../assets/images/media.jpg" slot = 'homenewsImg'/>
			<span slot = "authoron"class="home_zuo">作者：豆瓣电影</span>
		</Homenews>
		<Seek :seekShows = 'homeshow' @seeks = 'seeksshow'></Seek>
	</div>
</template>

<script>
import Topheader from '../../components/topheader'	
import Homebanner from './homebanner'	
import Hotspot from './hotspot'	
import Homenews from './Homenews'
import Seek from '../../components/seek'
export default{
	data:function(){
		return {
			placeholder:'影视 图书 唱片 小组等...',
			homeshow :false
		}
	},
	methods:{
		fn:function(){
			this.placeholder='收不到呀!'
		},
		seeks:function(){
			this.homeshow = true
		},
		seeksshow : function(vul){
			this.homeshow = vul
		}
	},
	components :{
		Topheader,  //中国移动
		Homebanner, //轮播图
		Hotspot,   	//热点
		Homenews,	//新闻
		Seek     	//搜索框
	}
}	
	
</script>

<style>
.homeWarg{position: relative;padding-bottom: 0.95rem;background: #F7F7F7;}
.homeWarg .homeTop{background: #42C055;position: fixed;top: 0.40rem;left: 0; z-index: 14;width: 100%;}
.homeWarg .homeTop .text{width:6.24rem;height: 0.58rem;text-indent: 2em;border-radius: 0.05rem;border:none;margin: 0.13rem;font-size: 0.24rem;}
.homeWarg .homeTop .searchImg{top: 0.27rem;left: 0.25rem;width: 0.36rem;position: absolute;}
.homeWarg .homeTop .scanImg{top: 0.27rem;right: 1rem;width: 0.36rem;position: absolute;}
.homeWarg .homeTop .chatImg{top: 0.18rem;right:0.16rem;width: 0.56rem;position: absolute;}
.homeWarg .tast_top{background: #42c055;color:#fff;}
.homeWarg .tast_top ul li{background: #fff;border-color:#fff;}
.homeWarg .tast_top ul .xinhao2{background: transparent;}
.homeWarg .home_zuo{color:#ccc;}
.homeWarg .home_zuoright{color:#ccc;margin-right: 0.36rem;}
</style>


