<template>
	<div class="audiotWarg">
		<Topheader></Topheader>
		<Myheader txt = '书影音' @myseek = myseeks>
			<img src="../../assets/images/ic_chat_green.png" slot = 'right'/>
			<img src="../../assets/images/ic_actionbar_search_icon.png" slot = 'left'/>
		</Myheader>
		<ul class="ulactive">
			<li :class="{active:liindex == 1 }" @click="liindex = 1" >
				<router-link to = "/audio/film">电影</router-link>
			</li>
			<li :class="{active:liindex == 2 }" @click="liindex = 2" >
				<router-link to = "/audio/study">读书</router-link>
			</li>
			<li :class="{active:liindex == 3 }" @click="liindex = 3">
				<router-link to = "/audio/television">电视</router-link>
			</li>
			<li :class="{active:liindex == 4 }" @click="liindex = 4">
				<router-link to = "/audio/city">同城</router-link>
			</li>
			<li :class="{active:liindex == 5 }" @click="liindex = 5">
				<router-link to = "/audio/music">音乐</router-link>
			</li>
		</ul>
		<router-view></router-view>
		<Seek :seekShows = 'mySeek' @seeks = 'seekmy'></Seek>
	</div>
</template>

<script>
	import Myheader from '../../components/myheader'
	import Topheader from '../../components/topheader'
	import Film from './film'
	import Study from './study'
	import Television from './television'
	import City from './city'
	import Music from './music'
	import Seek from '../../components/seek'
	export default {
		components :{
			Myheader:Myheader,
			Topheader,
			Film,
			Study,
			Television,
			City,
			Music,
			Seek
		},
		data : function (){
			return {
				liindex : 1,
				mySeek : ''
			}
		},
		methods : {
			active : function(){},
			myseeks : function(vul){
				this.mySeek = vul;
			},
			seekmy : function(vuls){
				this.mySeek = vuls;
			}
		}
	}
</script>

<style>
.audiotWarg{min-height: 11.85rem;padding-top: 1.66rem;}
.audiotWarg .ulactive {background: #fff; width: 100%;height: 0.76rem;border-bottom: 1px solid #e4e4e4;position: fixed;left: 0;top: 1.25rem;z-index: 20;}
.audiotWarg .ulactive li{width: 20%;float: left;height: 0.72rem;text-align: center;line-height: 0.72rem;}
.audiotWarg .ulactive li a{color:#999;text-decoration: none;font-size: 0.23rem;}
.audiotWarg .ulactive .active{border-bottom: 0.04rem solid #66bd76;}
.audiotWarg .ulactive .active a{color:#66bd76;}
.audiotWarg .filmWarg{background: #F7F7F7;}
.audiotWarg .seekAll .homeTops{background: #F7F8F7;color: #999;}
.audiotWarg .seekAll .homeTops span{color:#999;}
</style>


