import Vue from 'vue'
import Router from 'vue-router'
import Home from '../douban/home/home'
import Broadcast from '../douban/broadcast/broadcast'
import Audio from '../douban/audio/audio'
import Group from '../douban/group/group'
import Mine from '../douban/mine/mine'
import Film from '../douban/audio/film'
import Study from '../douban/audio/study'
import Television from '../douban/audio/television'
import City from '../douban/audio/city'
import Music from '../douban/audio/music'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/audio',
      name: 'Audio',
      component: Audio,
      children :[
      	{path : '' , component :  Film },
      	{path : 'film' , component :  Film },
				{path : 'study' , component :  Study },
				{path : 'television' , component : Television },
				{path : 'city' , component :  City },
				{path : 'music' , component : Music }
      ]
    },
    {
      path: '/broadcast',
      name: 'Broadcast',
      component: Broadcast
    },
    {
      path: '/group',
      name: 'Group',
      component: Group
    },
    {
      path: '/mine',
      name: 'Mine',
      component: Mine
    }
    
  ]
})



