<template>
	<div class="swiper-container" :class="swipeid">
		<div class="swiper-wrapper">
			<div class="swiper-slide"><slot name = 'swiper-con1'></slot></div>
			<div class="swiper-slide"><slot name = 'swiper-con2'></slot></div>
			<div class="swiper-slide"><slot name = 'swiper-con3'></slot></div>
 		</div>
 		<div :class="{ 'swiper-pagination' : pagination}" :style="{'text-align': pagintionDirection}"></div>
	</div>
</template>
<script>
import  "../../assets/basic/js/swiper.js"
export default {
	props :{
		swipeid :{//初始化
			type : String,
			default :''
		},
		effect :{ //切换效果
			type :String,
			default :'slide'
		},
		loop : { //环路
			type : Boolean,
			default : true
		},
		divection : { //
			type : String,
			default : 'horizontal'
		},
		autoplay : { 
			type : Number,
			default : 1000
		},
		paginationplay : { //分页器样式  
			type : String,
			default : 'bullets' //圆点        ‘fraction’  分式 ‘progress’  进度条 ‘custom’ 自定
		},
		pagination : { //是否使用分页器
			type : Boolean,
			default : true
		},
		pagintionDirection :{
			type : String,
			default : 'right' 
		}
	},
	mounted : function (){
		var _this = this;
		new Swiper ('.'+ _this.swipeid,{
			loop : _this.loop,
			pagination : '.swiper-pagination',
			paginationType : _this.paginationType,
			autoplay: _this.autoplay,
			direction : _this.direction,
			effect : _this.effect,
			autoplayDisableOnInteraction : false
		})
	}
}
</script>

<style>
	@import url("../../assets/basic/css/swiper.min.css");
	.swiper-container{margin-top: 0.84rem;}
	.swiper-container img {width: 100%;height: 2.38rem;}
</style>


