<template>
	<div class="groupfilmsWarg">
		<span @click="ok()">
			<img src="../../assets/images/ic_group_check_anonymous.png" class="g_img" v-show="blo"  />
			<img src="../../assets/images/ic_check_circle_pressed.png" class="g_img" v-show="!blo" />
		</span>
		<span class="groupfilms_img"><slot name = 'groupfilms_img'></slot></span>
		<div class="groupfilms_txt">
			<h1><slot name = 'gtou_txt_h'></slot></h1>
			<p><slot name = 'gtou_txt_p'></slot></p>
		</div>
		<div class="groupfilms_num">
			<span><slot name = 'groupfilms_num'></slot></span>
		</div>
	</div>
</template>

<script>
	export default {
		data : function(){
			return {
				blo : true
			}
		},
		methods : {
			ok : function(){
				if(this.blo == false){
					this.blo = true ;
					this.$emit('chenging',this.blo)
				}else{
					this.blo = !this.blo;
					this.$emit('chenging',this.blo)
				}
				
			}
		}
	}
</script>

<style>
.groupfilmsWarg{padding: 0.28rem;height: 0.96rem;}
.groupfilmsWarg .groupfilms_img{float: left;width: 0.97rem;height: 0.97rem;}	
.groupfilmsWarg .groupfilms_img img{float: left;width: 0.97rem;height: 0.97rem;}	
.groupfilmsWarg .groupfilms_txt{overflow: hidden;float: left;width: 3.65rem;height: 0.97rem;margin-left: 0.2rem;}	
.groupfilmsWarg .groupfilms_txt h1{overflow: hidden;font-size: 0.30rem;line-height: 0.40rem;height: 0.39rem;margin-top: 0.05rem;}	
.groupfilmsWarg .groupfilms_txt p{overflow: hidden;font-size: 0.20rem;line-height: 0.27rem;color: #999;margin-top: 0.02rem;}	
.groupfilmsWarg .groupfilms_num{float: right;color:#ccc;line-height: 0.97rem;font-size: 0.18rem;margin-right: 0.2rem;}	
.groupfilmsWarg .g_img{width: 0.48rem;height: 0.48rem;float: right;margin-top: 0.24rem;}
</style>


