<template>
	<div class="myheaderWarg">
		<span>{{ txt }}</span>
		<span class="searchImg" @click="sou()" ><slot name = 'left'></slot></span>
		<span class="chatImg"><slot name = 'right'></slot></span>
	</div>
</template>

<script>
	export default {
		data:function(){
			return {
				myshow :false,
				souShow : false
			}
		},
		props : ['txt'],
		methods : {
			sou : function(){
				
				this.souShow = true; //自定义值
				this.$emit('myseek',this.souShow) //传值给父亲
			}
		}
	}
</script>

<style>
.myheaderWarg{width: 100%; height: 0.84rem;line-height: 0.84rem;text-align: center;border-bottom: 1px solid #ccc;background: #F7F8F7;position: fixed;left: 0;top: 0.4rem;z-index: 15;}
.myheaderWarg span{font-size: 0.24rem;}
.myheaderWarg img{position: absolute;right: 0;top:0.18rem;width: 0.56rem;}
.searchImg img{right: 1.36rem;}
.chatImg img{right:0.16rem;}
</style>


